<script>
import { getData } from "../../api/data.js";
// import * as echarts from 'echarts'

export default {
  name: "home",

  data() {
    return {
      uploadHeaders: { token: this.$store.state.user.token },
      userInfo: {},
      wrongSubjects: {},
    };
  },
  methods: {
    //上传文件成功回调
    uploadSuccess(response, file, fileList) {
      console.log(response);
      this.userInfo.avatar = response.data;
      this.$store.commit("setUserInfo", this.userInfo);
    },
    //获得高频错题
    getWrongSujects() {
      this.$http
        .get("/courseSystem/wrongSubject/getAll", {
          headers: {
            token: this.$store.state.user.token,
          },
        })
        .then(({ data }) => {
          console.log(data);
          this.wrongSubjects = data.data
        });
    },
  },
  mounted() {},
  created() {
    //  this.$store.commit("setToken", data.token);
    this.$store.commit("getUserInfo");
    this.userInfo = this.$store.state.user.userInfo;
    if (this.userInfo.role === "teacher") {
      this.userInfo.role = "老师";
    }
    if (this.userInfo.role === "admin") {
      this.userInfo.role = "管理员";
    }
    this.getWrongSujects();
  },
};
</script>

